<template>
  <div id="home-page">
    <div class="headLine"></div>
    <header>
      <div class="headerInner">
        <div class="title">
          <span>巴布的小天地</span>
          <p>pugbabu</p>
        </div>
        <nav>
          <ul> 
            <li v-for="(item, index) in navList" :key="item.key" @click="change(index)">
                <i class="icon" :class="ind == index ? 'iconActive'+index : 'icon'+index"></i>
                <p :class="ind == index ? 'activeP' : ''">{{item.text}}</p>
            </li>        
          </ul>
        </nav>
      </div>
    </header>
    <div class="main">
      <router-view></router-view>
    </div>
    <footer>
      <div class="footerInner">
          <div class="copyRight">© 2017 <span class="loveIcon"></span> 雷大敏</div>
          <div class="count">本站访客数 1 人次  本站总访问量 1</div>
      </div>

    </footer>
    <div class="sideBar" @click="showTheSide">
      <span class="topLine"></span>
      <span class="midLine"></span>
      <span class="botLine"></span>
    </div>
    <div class="sideContent" v-show="isShowSide">
      <div class="close" @click="hideTheSide"></div>
    </div>
    <div class="pug"></div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        navList:[
          {'text': '首页', 'key': 1},
          {'text': '分类', 'key': 2},
          {'text': '关于', 'key': 3},
          {'text': '标签', 'key': 4},
          {'text': '搜索', 'key': 5} 
        ],
        ind: null,
        isShowSide: false    
      }
    }, 
    created(){

    },
    methods:{
      change(index) {
        this.ind = index;
        switch (index) {
          case 0:
            this.$router.push('/index')
            break;
          case 1:
            this.$router.push('/class')
            break;
          case 2:
            this.$router.push('/about')
            break;
          case 3:
            this.$router.push('/meta')
            break;
          case 4:
            this.$router.push('/search')
            break;
          default:
            break;
        }
      },
      showTheSide() {
        this.isShowSide = true;
      },
      hideTheSide() {
        this.isShowSide = false;
      }

    }
    
  }


</script>
<style lang="scss">
  #home-page{
    min-height: 100%;
    position: relative;
    background: #f0f0f0;   
    // background: #262a30;
    .headLine{
      height: 3px;
      background: #222;
    }
    header{
      .headerInner{
        margin: 0 auto;
        width: 700px;
        padding-top: 100px;
        height: 350px;
        box-sizing: border-box;
        .title{
          text-align: center;
          span{
            display: inline-block;
            width: 220px;
            height: 36px;
            line-height: 36px;
            background: #222;
            color: #fff;
            padding: 0 40px;
            box-sizing: border-box;
            font-weight: 700;
            font-size: 20px;
          }
          p{
            color: #999;
            font-size: 18px;
            margin-top: 10px;
            margin-bottom: 25px;
            height: 26px;
            line-height: 26px;           
          }        
        }
        nav{        
          text-align: center; 
          li{
            display: inline-block;
            margin: 0 10px;      
            padding-bottom: 5px;    
            cursor: pointer;          
            font-size: 13px;
            .icon{
              display: inline-block;
              width: 16px;
              height: 16px;
              margin-bottom: 10px;
              background-repeat: no-repeat;
              background-position: center center;
            }
            .icon0{
              background-image: url('../../static/images/home.png')
            }
            .iconActive0{
              background-image: url('../../static/images/homeActive.png')  
            }
            .icon1{
              background-image: url('../../static/images/class.png')
            }
            .iconActive1{
              background-image: url('../../static/images/classActive.png')  
            }
            .icon2{
              background-image: url('../../static/images/about.png')
            }
            .iconActive2{
              background-image: url('../../static/images/aboutActive.png')  
            }
            .icon3{
              background-image: url('../../static/images/meta.png')
            }
            .iconActive3{
              background-image: url('../../static/images/metaActive.png')  
            }
            .icon4{
              background-image: url('../../static/images/search.png')
            }
            .iconActive4{
              background-image: url('../../static/images/searchActive.png')  
            }
            .activeP{
              color: #1296db;
            }
          }
          li:hover{
            border-bottom: 1px solid #707070;
          }
        }
      }
    }
    .main{
      width: 100%;
      background: #f0f0f0;
      box-sizing: border-box;
      height: 100%;
      padding-bottom: 96px;
    }


    footer{
      position: absolute;
      width: 100%;
      bottom: 0;
      height: 96px;
      left: 0;
      color: #999;
      // background: #222;
      .footerInner{
        width: 700px;
        margin: 20px auto;
        text-align: center;
        .loveIcon{
          display: inline-block;
          width: 16px;
          height: 16px;
          background: url("../../static/images/love1.png") no-repeat center;
          vertical-align: -1px;
        }
        .count{
          color: #999;
          font-size: 14px;
          height: 28px;
          line-height: 28px;
          margin-top: 10px;
        }
      }
    }
    .sideBar{
      position: fixed;
      right: 30px;
      bottom: 45px;
      box-sizing: border-box;
      width: 28px;
      height: 28px;
      padding: 2px;
      background-color: #222;;
      cursor: pointer;
      span{
        display:block;
        width: 24px;
        height: 2px;
        background: #fff;
        left: 2px;
        position: absolute;
      }
      .topLine{
        top: 6px;
      }
      .midLine{
        top:13px;
      }
      .botLine{
        top: 20px;
      }
    }
    .sideContent{
      position: fixed;
      height: 100%;
      width: 200px;
      right: 0;
      top:0;
      background: #222;
      .close{
        width: 16px;
        height: 16px;
        background: url("../../static/images/close.png") no-repeat center;
        position: absolute;
        right:30px;
        bottom: 45px;
        cursor: pointer;
      }
    }
    .pug{
      position: fixed;
      left: 0;
      bottom: 3px;
      width: 180px;
      height: 180px;
      background: url("../../static/images/pug.png") no-repeat center;
    }
   
  }
</style>



